<template lang="html">
  <div class="container">

    <div id="particles-js"></div>
    <div id="wrapper">
      <div>

        <!--<img  width="100" height="100" src="../assets/image/lanbitoulogo.png" />-->
        <p>记录下生活的点滴，工作的思绪!</p>
      </div>
      <nav class="switch_nav">
        <a href="javascript:;" id="switch_login" class="switch_btn on">烂笔头</a>
        <div class="switch_bottom" id="switch_bottom"></div>
      </nav>
      <div id="login">
        <ul class="group_input">

            <input type="text" v-model="mail"    placeholder="邮箱" />

            <input type="text" v-model="code"   placeholder="验证码" />


          <button  class="submit_btn" color="blue"  v-show="show" v-on:click="getCode">发送验证码</button>
          <button class="submit_btn" v-show="!show">{{count}} s</button>

        </ul>


      </div>







      <button @click="deletePerson()"  class="submit_btn">登陆</button>
    </div>

    <p> 推荐使用APP【扫码登录】 </p>
    <img   v-bind:src="codeImgUrl"/>


  </div>
</template>

<script>

  // import "../assets/libs/particles/css/style.css"
  // import "../assets/css/base.css"
  import "../assets/css/login.css"
  import "../assets/libs/jquery-1.12.4/jquery.min.js"

  export default {

    data () {


      return {
        code: '',
        mail:'',
        show:true,
        timer:null,
        gcode:'212321',
        gmail:'111@https://waitclopeo.com',
        count:'',
        timer:null,
        timer1:null,
        timer2:null,
        uuid:'',

        codeImgUrl:'',
      }
    },
    components: {

      // List
    },
    created () {
      this.cookieLogin();
      let that = this;
      that.getUuid();
      this.timer1 = setInterval( () => {
        that.getUuid();


      },30 * 1000);






      this.timer2 = setInterval( () => {

        this.$http.post('/jinjiapi/getScanLogin', {uuid: this.uuid}).then(res => {
          res = res.body;


          if (res.status) {

            clearInterval(that.timer2);
            clearInterval(that.timer1);
            localStorage.setItem('userId', res.userId);
            that.$router.push({path: '/list', query: {selected: "2"}});

            that.$router.push({

              name: 'list',
              params: {
                userId: res.userId,

              }

            });
          } else {
          }

        });


      }, 2*1000);



    },
    destroyed: function () {



      clearInterval(this.timer);
      this.timer = null;
      clearInterval(this.timer1);
      this.timer1 = null;
      clearInterval(this.timer2);
      this.timer2 = null;
      console.log('页面跳转走了');
    },

    methods:{

      cookieLogin() {
        let userId = localStorage.getItem('userId');
        if (userId && userId.length > 0){
          console.log('cookie:', userId);
        this.$router.push({path: '/list', query: {selected: "2"}});

        this.$router.push({

          name: 'list',
          params: {
            userId: userId,

          }

        });
      }
      },

      getUuid() {
        var s = [];
        var hexDigits = "0123456789abcdef";
        for (var i = 0; i < 36; i++) {
          s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
        }
        s[14] = "4";  // bits 12-15 of the time_hi_and_version field to 0010
        s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1);  // bits 6-7 of the clock_seq_hi_and_reserved to 01
        s[8] = s[13] = s[18] = s[23] = "-";

        this.uuid = s.join("");
        this.codeImgUrl = "https://waitclopeo.com/create_qrcode?uuid="+this.uuid;
        console.log(this.uuid);
      },



      createPerson: function(){
        this.people.push(this.newPerson);
        // 添加完newPerson对象后，重置newPerson对象
        this.newPerson = {name: '', age: 0, sex: 'Male'}
      },
      deletePerson: function() {





        if(!this.code || this.code === "" ||this.code.length !==6 ){
          alert('验证码不正确，请重新输入');
          return;
        }
        var regExp = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
        if (!regExp.test(this.mail)) {
          alert("您输入的E-mail地址不正确!");

          return;
        }
        if(this.mail === this.gmail && this.code===this.gcode) {
          localStorage.setItem('userId', this.gmail);

          this.$router.push({path: '/list', query: {selected: "2"}});

          this.$router.push({

            name: 'list',
            params: {
              userId: this.gmail,
              type: '3',
              // id:1
            }

          });
        }else {
          alert('邮箱或验证码不正确哦！');
        }








      },
      getCode(){
        var regExp = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
        if (!regExp.test(this.mail)) {
          alert("您输入的E-mail地址不正确!");

          return;
        }
        this.$http.post('/jinjiapi/getMailCode', {mail: this.mail}).then(res => {
          res = res.body;
          console.log(res);

          if (res.status) {
            alert('验证码已经发送，请注意查收(部分邮箱会误报如垃圾箱，如无收到请检查垃圾箱)');
            this.gmail = res.mail;
            this.gcode = res.code;
          } else {
            alert(res.message);
          }

        });

        this.show = false;
        const TIME_COUNT = 60;
        if (!this.timer) {
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.show = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1000)
        }
      }



    },
  }
</script>

<style lang="css" scoped>
  .container {
    width: 100%;
    margin: 0 auto;
  }
  .content {
    margin-bottom: 1.8rem;
  }
  .cont-ul {
    padding-top: 0.5rem;
    background-color: #ccc;
  }
  .cont-ul::after {
    content: '';
    display: block;
    clear: both;
    width: 0;
    height: 0;
  }
  #page
  {
    margin:0 25% 0 25%;
    padding: 10px 10px;
    border:3px solid green;
    height:500px;
  }
  .flex-wrapper {
    width: 100%;
    height: 56px;
    margin-top: 8px;
  }
  .flex-demo {
    width: 200px;
    height: 32px;
    background-color: #e0e0e0;
    text-align: center;
    line-height: 32px;
    margin-left: 8px;
  }
  .flex-wrapper:first-child {
    margin-top: 0;
  }
  .flex-demo:first-child {
    margin-left: 0;
  }
</style>



